.subscribe-now{
    padding: 80px 0;
    .subscribe-now__content{
        .subscribe-now__content__title{
            font-family: $interFont;
            line-height: 3.75;
            text-align: left;
            color: $colorYellow;
            font-weight: bold;
            margin-bottom: -15px;
        }
        .subscribe-now__content__quote{
            font-family: $georgiaFont;
            font-weight: bold;
            line-height: 1.25;
            text-align: left;
            color: $colorBlue;
            font-size: calc( 42px + ((48 - 42 ) * ((100vw - 1366px ) / (1920 - 1366 ))));
            margin-bottom: 26px;
        }
        .subscribe-now__content__form{
            position: relative;
            input{
                outline: 0;
                border: 1px solid #bdbdbd;
                border-radius: 4px;
                padding: 15px;
                width: 100%;
                padding-left: 35px;
                &::placeholder{
                    font-family: $interFont;
                    font-size: 16px;
                    line-height: 3.75;
                    text-align: left;
                    color: #bdbdbd;
                }
            }
            button{
                @extend.my-btn;
                position: absolute;
                right: 6px;
                top: 6px;
                font-size: 16px;
                font-weight: 500;
                line-height: 1.31;
                text-align: center;
                padding: 10px 25px;
            }
            .icon-message{
                position: absolute;
                left: 15px;
                top: 20px;
            }
        }
    }
}
@media only  screen and (max-width:768px) {
    .subscribe-now{
        .subscribe-now__content{
            .subscribe-now__content__form{
            // padding: 15px ;
            button{
                padding: 10px 10px;
            }
        }
        }
        
    }
}
@media only screen and (min-width:1024px) {
    .subscribe-now {
        .subscribe-now__content{
            .subscribe-now__content__quote{
                width: 75%;
            }
        }
    }  
}
    